


body {
    margin: 0;
    font-size: 16px; }


h1, h2 {
    margin-bottom: 1.5rem; }

h1 {
    @include rfs(30); }

h2 {
    @include rfs(24); }

p {
    @include rfs(17);
    margin-bottom: 1.5rem; }


a {
    @include transition();
    &:hover {
        @include transition(); } }

.container-md {
    padding-left: 2rem;
    padding-right: 2rem;
    @include media-breakpoint-up(md) {
        padding-left: 3rem;
        padding-right: 3rem; }
    @include media-breakpoint-up(lg) {
        margin: 3rem auto;
        padding-left: 5rem;
        padding-right: 5rem; } }
.col {
    padding: 2rem;
    @include media-breakpoint-up(md) {
        padding-left: 3rem;
        padding-right: 1rem; }
    @include media-breakpoint-up(lg) {
        padding-bottom: 0;
        margin: 0; }
    @include media-breakpoint-up(xl) {
        padding: 3rem; } }

header {
    .row {
        max-width: $bp-xxxl;
        height: 5rem; }
    .logo {
        height: 100%;
        width: auto; }
    @include media-breakpoint-down(md) {
        .row {
            height: 3rem; } } }

main {
    max-width: $bp-xl;
    margin: auto; }


footer {
    align-items: center;
    justify-content: center;
    margin: auto;
    position: relative;
    display: flex;
    flex-direction: column;
    ul {
        width: $bp-xl;
        max-width: 100%;
        position: absolute;
        margin-left: 2rem;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        list-style: none;
        li {
            margin: 1rem;
            a {
                color: white;
                font-weight: 300;
                @include rfs(22);
                opacity: .75;
                &:hover {
                    opacity: 1;
                    color: $yellow;
                    text-decoration: none; } } }
        @include media-breakpoint-down(md) {
            flex-direction: row;
            flex-wrap: wrap;
            display: flex;
            li {
                margin: 0 auto;
                a {
                    @include rfs(16); } } } } }


.halves {
    padding: 0;
    @include media-breakpoint-up(lg) {
        img {
            position: absolute;
            min-width: 1000%;
            min-height: 1000%;
            width: auto;
            height: auto;
            max-width: none;
            max-height: none;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%) scale(.1); }
        .imgcol {
            overflow: hidden; }
        div {
            &:not(.imgcol) {
                padding-top: 3rem;
                padding-bottom: 3rem;
                p, h1, h2 {
                    padding-right: 1rem;
                    padding-left: 1rem; } } } } }





div {
    &.bg-secondary {
        color: $primary; } }

div {
    &.bg-primary {
        color: $secondary; } }


.stage-wrapper {
    position: relative; }

.stage-text {
    position: absolute;
    left: 0;
    color: white !important;
    @include media-breakpoint-up(xxxs) {
        @include rfs(18);
        padding-left: 2rem;
        &.first {
            top: 50%;
            min-width: 8rem; }
        &.second {
            font-weight: 700;
            top: 65%;
            min-width: 8rem; }
        &.third {
            font-weight: 700;
            top: 80%;
            min-width: 10rem; } }

    @include media-breakpoint-up(xs) {
        @include rfs(26);
        padding-left: 3rem;
        &.first {
            top: 50%;
            min-width: 11rem; }
        &.second {
            top: 65%;
            min-width: 11rem; }
        &.third {
            top: 80%;
            min-width: 15rem; } }
    @include media-breakpoint-up(md) {
        @include rfs(35);
        &.first {
            min-width: 12rem; }
        &.second {
            min-width: 12rem; }
        &.third {
            min-width: 15rem; } }

    @include media-breakpoint-up(lg) {
        @include rfs(45);
        padding-left: 9rem;
        &.first {
            min-width: 23rem; }
        &.second {
            min-width: 23rem; }
        &.third {
            min-width: 28rem; } }

    @include media-breakpoint-up(xl) {
        @include rfs(50);
        padding-left: 11rem;
        &.first {
            min-width: 27rem; }
        &.second {
            min-width: 27rem; }
        &.third {
            min-width: 35rem; } }

    @include media-breakpoint-up(xxl) {
        @include rfs(67);
        &.first {
            top: 50%;
            min-width: 35rem; }
        &.second {
            top: 65%;
            min-width: 35rem; }
        &.third {
            top: 80%;
            min-width: 42.5rem; } }
    @include media-breakpoint-up(xxxl) {
        @include rfs(85);
        &.first {
            min-width: 42.5rem; }
        &.second {
            min-width: 42.5rem; }
        &.third {
            min-width: 50rem; } } }

.stoerer-box {
    position: relative;
    margin-top: 5rem;
    > img {
        position: absolute;
        right: 5%;
        max-width: 13rem;
        min-width: 7rem;
        width: 20%;
        bottom: 85%; }

    @include media-breakpoint-down(sm) {
        margin-top: 5rem;
        img {
            bottom: 94%; } }
    @include media-breakpoint-down(xs) {
        margin-top: 3rem;
        img {
            bottom: 97%; } } }


